@mixin dimensions($width, $height) {
  width: $width;
  height: $height;
}

@mixin possitionTopLeft($position, $top, $left) {
  position: $position;
  top: $top;
  left: $left;
}

@mixin image($url, $position, $size) {
  background: url($url) no-repeat;
  background-position: $position;
  background-size: $size;
}

@mixin font($family, $color, $size, $transform, $align) {
  font-family: $family;
  color: $color;
  font-size: $size;
  text-transform: $transform;
  text-align: $align;
}

.App {
  user-select: none;

  h2,
  h3 {
    @include font('Asap-Bold', #7b7b7b, 3.5vw, uppercase, left);
    letter-spacing: 0.5vw;
  }

  h3 {
    @include possitionTopLeft(relative, 1vw, 50%);
    text-align: center;
    transform: translate(-50%);
    margin-bottom: 5vw;
  }

  h3:before {
    content: '';
    @include possitionTopLeft(absolute, -1vw, 50%);
    transform: translate(-50%);
    @include dimensions(5vw, 1vw);
    border-top: #7b7b7b solid 0.3vw;
  }

  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  span {
    margin: 0;
  }

  button {
    @include font('Asap-Medium', #7b7b7b, 1.2vw, uppercase, center);
    font-weight: bold;
    letter-spacing: 0.1vw;
    background: none;
    border: solid 0.1vw #7b7b7b;
    padding: 1vw 3vw;

    cursor: pointer;
  }

  button:hover {
    background-color: white;
    border-color: white;
    box-shadow: 0 1vw 1vw #d3d3d3;
  }

}

